import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CalendarComponent } from 'src/app/components/user/calendar/calendar.component';
import { ExtractComponent } from 'src/app/components/user/extract/extract.component';
import { ScheduleComponent } from 'src/app/components/user/schedule/schedule.component';
import { MySchedulesComponent } from 'src/app/components/user/my-schedules/my-schedules.component';
import { UserComponent } from './user.component';
import { BuyComponent } from 'src/app/components/user/buy/buy.component';

const routes: Routes = [
  {
    path: '',
    component: UserComponent,
  },
  {
    path: 'schedule',
    component: ScheduleComponent
  },
  {
    path: 'extract',
    component: ExtractComponent
  },
  {
    path: 'my-schedules',
    component: MySchedulesComponent
  },
  {
    path: 'calendar',
    component: CalendarComponent
  },
  {
    path: 'buy',
    component: BuyComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UserRoutingModule { }
